<script>
import { mapActions, mapMutations, mapGetters } from "vuex";
import notificacion from "@/mixin/notificacion";
export default {
  name: "ListaCapitulos",
  mixins: [notificacion],
  props: {
    seleccionable: {
      type: Boolean,
      default: true
    },
    editable: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      state: "not_accepted",
      active: null,
      id_temporada: "",
      titulo: "",
      duracion: "",
      idioma: "",
      subtitulos: "",
      director: "",
      idSerie: "",
      sinopsis: "",
      id: "",
      eliminacionTotal: false,
      personalizar: false
    };
  },
  computed: {
    ...mapGetters("Catalogo", ["contenido", "temporada", "capitulos"]),
    ...mapGetters(["cargando"]),
    getCapitulo() {
      return {
        id_temporada: this.id_temporada,
        titulo: this.titulo,
        duracion: this.duracion,
        idioma: this.idioma,
        subtitulos: this.subtitulos,
        director: this.director,
        sinopsis: this.sinopsis,
        id: this.id,
        idSerie: this.$route.params.id
      };
    }
  },
  methods: {
    ...mapMutations("Catalogo", ["updateCapitulo"]),
    ...mapActions("Catalogo", [
      "updateCapitulos",
      "modifyCapitulo",
      "deleteCapitulo",
      "updateCapitulos"
    ]),
    selCapitulo(index) {
      this.active = index;
      this.updateCapitulo(this.capitulos[index]);
      this.titulo = this.capitulos[index].titulo;
      this.sinopsis = this.capitulos[index].sinopsis;
      this.id = this.capitulos[index].id;
      this.id_temporada = this.capitulos[index].id_temporada;
      this.titulo = this.capitulos[index].titulo;
      this.duracion = this.capitulos[index].duracion;
      this.idioma = this.capitulos[index].idioma;
      this.subtitulos = this.capitulos[index].subtitulos;
      this.director = this.capitulos[index].director;
      this.sinopsis = this.capitulos[index].sinopsis;
    },
    items(capitulo) {
      return [
        {
          Titulo: capitulo.titulo,
          Director: capitulo.director,
          Subtitulos: capitulo.subtitulos,
          Duracion: capitulo.duracion,
          Sinopsis: capitulo.sinopsis,
          Temporada: capitulo.tempTitulo
        }
      ];
    },
    async modificarCapitulo() {
      const resultado = await this.modifyCapitulo(this.getCapitulo);
      this.show(resultado, "capitulo", "modificado");
    },
    async eliminarCapitulo() {
      const resultado = await this.deleteCapitulo(this.getCapitulo);
      this.show(resultado, "capitulo", "eliminado");
    }
  }
};
</script>

